import LC from 'leancloud-storage';  // 导入 leancloud 数据存储模块
import config from './config/lc_config';  // 导入 leancloud 的应用keys

let initOptions;  // leancloud 初始化参数

if (config.url.trim() === '') {
    // url 为空的时候，访问国际版：leancloud.app
    initOptions = {
        appId: config.id,
        appKey: config.key,
    };
} else {
    // url 不为空的时候，访问国内版:leancloud.cn
    initOptions = {
        appId: config.id,
        appKey: config.key,
        serverURLs: config.url,
    };
}
// 执行 leancloud 初始化
LC.init(initOptions);

export default {};