import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter)

const routes = [
  {
    path: '/',  // 匹配的路径
    alias: '/home',  // 路由的别名，相当于另一个匹配的路径
    // name: 'Home',  // 命名路由
    // 将 src\views\home\Index.vue 更改为 @/views/home/Index.vue
    // @ 是  src 文件夹的别名
    component: () => import('@/views/home/Index.vue'),  // 路由对应的组件 
    children:[
      {
        path:'',     // 实际路径： /
        name:'BoardList',
        component:()=>import('@/views/home/pages/BoardList.vue'),
      },
      {
        path:'board/:id',  // 实际路径： /board/:id
        name:'Board',
        component:()=>import('@/views/home/pages/Board.vue'),
      },
      {
        path:'topicdetail/:id',  // 实际路径： /topicdetail/:id
        name:'TopicDetail',
        component:()=>import('@/views/home/pages/TopicDetail.vue'),
      },

    ]
  },
  {
    path: '/dashboard',  // 一级路由以 / 开头
    // name: 'Dashboard', // 有默认子路由时,父路由不需要 name 属性
    component: () => import('@/views/dashboard/Index.vue'),
    // 仪表盘的子路由
    children:[
      {
        path:'',  // 实际路径： /dashboard/
        alias:'dashboard',  // 实际路径： /dashboard/dashboard
        name: 'Dashboard',
        component:()=>import('@/views/dashboard/pages/Dashboard.vue'),
      },
      {
        path:'counter',   // 实际路径： /dashboard/counter
        name:'Counter',
        component:()=>import('@/views/dashboard/pages/Counter.vue'),
      },
      {
        path:'createtopic',   //  实际路径： /dashboard/createtopic
        name:'CreateTopic',
        component:()=>import('@/views/dashboard/pages/CreateTopic.vue'),
      },
      {
        path:'createcategory',   //  实际路径： /dashboard/createcategory
        name:'CreateCategory',
        component:()=>import('@/views/dashboard/pages/CreateCategory.vue'),
      },
      {
        path:'categorymanager', //  实际路径： /dashboard/categorymanager
        name:'CategoryManager',
        component:()=>import('@/views/dashboard/pages/CategoryManager.vue'),
      },
      {
        path:'topicmanager', //  实际路径： /dashboard/topicmanager
        name:'TopicManager',
        component:()=>import('@/views/dashboard/pages/TopicManager.vue'),
      },
      {
        path:'userinfo', //  实际路径： /dashboard/userinfo
        name:'UserInfo',
        component:()=>import('@/views/account/pages/UserInfo.vue'),
      },
    ]
  },
  {
    path: '/account',
    // name: 'Account', // 有默认子路由时,父路由不需要 name 属性
    component: () => import('@/views/account/Index.vue'),
    // 二级路由
    children:[
      {
        path:'',  // 子路由不能以 / 开头， 匹配的路径，实际路径为：/account/
        alias:'login',  // 路径的别名, 实际路径为：/account/login
        name: 'Login',  // 命名路由
        component:()=>import('@/views/account/pages/Login.vue'),  // 路由对应的组件
      },
      {
        path:'signup',  // 匹配的路径。多级路由的完整路径为父路由与子路由的组合。实际路径：/account/signup
        name:'SignUp',
        component:()=>import('@/views/account/pages/SignUp.vue'),
      },
    ],
  }
];

const router = new VueRouter({
  routes
});

// 使用路由拦截保存跳转到登录页的地址，登录成功后跳转回该页(回跳功能在登录页实现)
router.beforeEach((to, from, next) => {
  // 如果 跳转的目标 path 是登录页（看上面的路由设置， ）
  if (to.path == '/account' || to.path == '/account/login') {
    // 保存当前路由， 用于登录成功后跳转到登录前的页面
    localStorage.setItem("preRoute", router.currentRoute.fullPath);
  }
  next();
});

export default router;
