import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

// 使用 this.$store  引用此处定义的对象 
export default new Vuex.Store({
  // 状态（共享变量）
  state: {
    count: 100,
    drawer: null,
    currentUser: null,    
  },

  // 定义用于修改状态的方法
  mutations: {
    // 加一方法, num 实际传入的参数
    increment(state, num) {
      state.count += num;
    },

    // 用于修改状态的方法
    SET_DRAWER(state, val) {
      state.drawer = val;
    },

    SET_CURRENT_USER(state, val) {
      state.currentUser = val;
    },

  },
  actions: {
  },
  modules: {
  }
});
